  <div class="page" id="page" @click="clickTips">
      <div class="wrapper">
        <el-row>
            <el-col :xs="24" :sm="4" :md="18" :lg="18" :xl="18">
              <div class="main">
                <p class="title">About</p>
                <!-- <list_article :items="items" type="archives"></list_article> -->
                <div class="card">
                  <p class="card-title" id="anchor-about"></p>
<!--                  <p class="txt">2</p>-->
<!--                </div>-->
<!--                <div class="card">-->
<!--                  <p class="card-title" id="anchor-contact"></p>-->
                  <ul style="padding-top: 2px">
                    <li><a href="http://www.mrdiv.top:8081" target="_blank">Mr Div</a></li>
<!--                    <li>Github<a href="https://github.com/bestRenekton" target="_blank">bestRenekton</a></li>-->
<!--                    <li><a href="https://www.zhihu.com/people/yang-pang-zi-40/activities" target="_blank"></a></li>-->
